.page-header {
  box-sizing: border-box;
  padding: 35px 103px 0px 103px;
  margin-bottom: 108px;

  min-height: 650px;
  min-width: 320px;

  background-image: url("../../img/main-bg-desktop@1x.jpg");
  background-color: #3978b5;
  background-size: cover;

  color: #fff;

  .main-nav {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 210px;
    box-sizing: border-box;
  }
  .main-nav__toggle {
    position: absolute;
    width: 24px;
    height: 14px;
    background: transparent;
    top: 20px;
    right: 15px;
    border: 0;
    visibility: hidden;
    opacity: 0;
    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 24px;
      height: 14px;
    }
  }
  .main-nav--closed .main-nav__toggle::before {
    background: url("../../img/icon-burger.svg");
  }
  .main-nav--opened .main-nav__toggle::before {
    background: url("../../img/icon-close.svg") no-repeat center;
  }
  .promo {
    margin-left: 95px;
  }
  .promo__title {
    margin: 0;
    padding: 0;
    margin-bottom: 30px;

    font-size: 54px;
    font-weight: 700;
  }
  .promo__description {
    margin: 0;
    padding: 0;

    font-size: 20px;
  }
}
@media (min-width: $desktop-max-width) {
  .page-header {
    padding: 0 calc(50vw - 580px);
    padding-top: 35px;
  }
}
@media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
  .page-header {
    background-image: url("../../img/main-bg-desktop@2x.jpg");
    background-size: cover;
  }
}
@media (max-width: $tablet-width) {
  .page-header {
    padding: 35px 60px 0px 60px;
    margin-bottom: 100px;

    background-image: url("../../img/main-bg-tablet@1x.jpg");
    .main-nav {
      .logo {
        img {
          width: 38px;
          height: 20px;
        }
      }
    }
    .promo {
      margin: 0;
    }
  }
}
@media (max-width: $tablet-width) and (min-resolution: $retina-dpi),
  (max-width: $tablet-width) and (min-resolution: $retina-dppx) {
  .page-header {
    background-image: url("../../img/main-bg-tablet@2x.jpg");
  }
}

@media (max-width: $mobile-width) {
  .page-header {
    // padding: 0 15px;
    padding: 0;
    // display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: url("../../img/main-bg-mobile@1x.jpg");
    .main-nav {
      margin: 0;
      padding: 20px 15px 0px 15px;

      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      &--closed,
      &--opened {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        min-height: 299px;
      }
      &--closed .site-navigation-item {
        display: none;
      }
      &--opened .site-navigation-item {
        display: flex;
        flex-direction: column;
      }
      &--opened {
        background-color: $primary-color;
        a {
          color: $secondary-color;
          svg {
            path {
              fill: $secondary-color;
            }
          }
        }
      }
      &--closed+.promo,
      &--opened+.promo {
        padding-top: 350px;
      }
      &--nojs {
        position: static;
        background-color: $primary-color;
        a {
          color: $secondary-color;
          svg {
            path {
              fill: $secondary-color;
            }
          }
        }
      }
      &--nojs .site-navigation-item {
        display: flex;
        flex-direction: column;
      }
      &--nojs .main-nav__toggle {
        display: none;
      }
      &--nojs+.promo {
        padding-top: 0;
      }
    }
    .main-nav__toggle {
      visibility: visible;
      opacity: 1;
    }
    .logo {
      margin-bottom: 30px;
    }
    .promo {
      padding: 0 15px;
    }
    .promo__title {
      font-size: 36px;
    }
  }
}
@media (max-width: $mobile-width) and (min-resolution: $retina-dpi),
  (max-width: $mobile-width) and (min-resolution: $retina-dppx) {
  .page-header {
    background-image: url("../../img/main-bg-mobile@2x.jpg");
  }
}

